/* to remove arrow from numver  */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
/* for firefox */
input[type=number]{
  -moz-appearance: textfield;
}

#header {
    top: 150px;
    position: relative;
    margin: auto;
  text-align: center;
    color: #F1C232;
    font-family: 'Roboto';
  }
#loadingCircles {
    position: relative;
    width: 60px;
    margin: auto;
    top: -110px;
}

    .loadingCircle {
      height: 4px;
      position: relative;
      width: 4px;
      background: #F1C232;
      border-radius: 50%;
      box-shadow: 0 2px 4px 0 rgba(0,0,0,0.125);
    }

    #loadingCircle1{
      position: absolute;
      left: 50px;
      top: 0px;
      -webkit-animation: moveInCircle 2s infinite ease-in-out;
              animation: moveInCircle 2s infinite ease-in-out;
    }

    #loadingCircle2 {
      position: absolute;
      left: 0px;
      top: -50px;
      -webkit-animation: moveInCircle 2s infinite ease-in-out;
              animation: moveInCircle 2s infinite ease-in-out;
      -webkit-animation-delay: -1s;
              animation-delay: -1s;
    }

    #loadingCircle3 {
      position: absolute;
      left: 50px;
      top: -50px;
      -webkit-animation: moveInCircle 2s infinite ease-in-out;
              animation: moveInCircle 2s infinite ease-in-out;
      -webkit-animation-delay: -0.5s;
              animation-delay: -0.5s;
    }

    #loadingCircle4 {
      position: absolute;
      left: 0px;
      top: 0px;
      -webkit-animation: moveInCircle 2s infinite ease-in-out;
              animation: moveInCircle 2s infinite ease-in-out;
      -webkit-animation-delay: -1.5s;
              animation-delay: -1.5s;
    }
  
@-webkit-keyframes moveInCircle{
  0%{
    left:50px;
    top: 0px;
  }
  25%{
    left: 0px;
    top: 0px;
  }
  50%{
    top: -50px;
    -webkit-transform: scale(3);
            transform: scale(3);
    left: 0px;
  }
  75%{
    top: -50px;
    left: 50px;
  }
  100%{
    top: 0px;
    -webkit-transform: scale(1);
            transform: scale(1);
    left: 50px;
  }
}
  
@keyframes moveInCircle{
  0%{
    left:50px;
    top: 0px;
  }
  25%{
    left: 0px;
    top: 0px;
  }
  50%{
    top: -50px;
    -webkit-transform: scale(3);
            transform: scale(3);
    left: 0px;
  }
  75%{
    top: -50px;
    left: 50px;
  }
  100%{
    top: 0px;
    -webkit-transform: scale(1);
            transform: scale(1);
    left: 50px;
  }
}


